import { Box, Divider, FormControl, InputLabel, MenuItem, Select, Stack, Typography } from '@mui/material'
import EditInput from 'components/EditInput'
import { PERMISSION } from 'interfaces'
import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getGroups } from 'redux/slices/Group'
import PermissionItem from './PermissionItem'
import { LoadingButton } from '@mui/lab'
import SaveIcon from '@mui/icons-material/Save'
import { GroupApi } from 'API/group'
import { UIAction } from 'redux/slices/UI'
import AccountList from './Account/AccountList'

export default function Detail() {
	const { id } = useParams<{ id: string }>()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const group = useAppSelector((s) => s.Group.list).find((f) => f.id === Number(id))
	const status = useAppSelector((s) => s.Group.status)
	const [loading, setLoading] = useState(false)
	const [permission, setPermission] = useState<number[]>([])
	const dispatch = useAppDispatch()
	useEffect(() => {
		if (status === 'init') dispatch(getGroups())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	useEffect(() => {
		if (group) {
			setPermission(group.permission)
		}
	}, [group])

	if (!id || !group || !tokenInfo) return null
	return (
		<Stack gap={1}>
			<Stack gap={1} border="1px solid #eee" p={1}>
				<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
					<Typography
						color="primary.dark"
						variant="h6"
						sx={{
							fontWeight: 600,
						}}
					>
						基本情報
					</Typography>
				</Stack>
				<Stack>
					<EditInput
						label="グループ名"
						value={group.name}
						apiEndpoint={'/api/group/' + id}
						fieldName="name"
						readOnly={tokenInfo.permissions[PERMISSION.ACCOUNT] < 2}
					/>
					<EditInput
						label="説明"
						value={group.description}
						apiEndpoint={'/api/group/' + id}
						fieldName="description"
						readOnly={tokenInfo.permissions[PERMISSION.ACCOUNT] < 2}
					/>
				</Stack>
				<Typography
					color="primary.dark"
					sx={{
						fontWeight: 600,
					}}
				>
					権限情報
				</Typography>
				<Stack direction="row" justifyContent="flex-end">
					<FormControl
						fullWidth
						size="small"
						sx={{
							maxWidth: 200,
						}}
					>
						<InputLabel>権限テンプレート</InputLabel>
						<Select
							value={permission.join('')}
							label="TEMPLATE PERMISSION"
							onChange={(e) => {
								if (e.target.value) setPermission(e.target.value.split('').map((v) => parseInt(v)))
							}}
						>
							<MenuItem value={'7777770'}>消費者庁権限</MenuItem>
							<MenuItem value={'0070000'}>消費者団体権限</MenuItem>
						</Select>
					</FormControl>
				</Stack>
				<table>
					<thead>
						<Box component="tr">
							<Box
								component="th"
								sx={{
									width: 100 / 7 + '%',
								}}
							>
								CMS
							</Box>
							<Box
								component="th"
								sx={{
									width: 100 / 7 + '%',
								}}
							>
								フォーム
							</Box>
							<Box
								component="th"
								sx={{
									width: 100 / 7 + '%',
								}}
							>
								メール
							</Box>
							<Box
								component="th"
								sx={{
									width: 100 / 7 + '%',
								}}
							>
								メーリングリスト
							</Box>
							<Box
								component="th"
								sx={{
									width: 100 / 7 + '%',
								}}
							>
								アカウント
							</Box>
							<Box
								component="th"
								sx={{
									width: 100 / 7 + '%',
								}}
							>
								フォーム回答
							</Box>
						</Box>
					</thead>
					<tbody>
						<Box component="tr">
							{permission.map((p, i) => {
								if (i > 5) return null
								return (
									<Box gap={1} component="td" key={i}>
										<Stack>
											<PermissionItem
												active={[1, 3, 5, 7].includes(p)}
												text="閲覧"
												handleClick={() => {
													const tmp = [...permission]
													if ([1, 3, 5, 7].includes(p)) {
														tmp[i] = 0
													} else {
														tmp[i] += 1
													}
													setPermission(tmp)
												}}
											/>
											<PermissionItem
												active={[2, 3, 6, 7].includes(p)}
												text="編集"
												handleClick={() => {
													const tmp = [...permission]
													if ([2, 3, 6, 7].includes(p)) {
														tmp[i] -= 2
													} else {
														tmp[i] += tmp[i] > 0 ? 2 : 3
													}
													setPermission(tmp)
												}}
											/>
											<PermissionItem
												active={[4, 5, 6, 7].includes(p)}
												text="実行"
												handleClick={() => {
													const tmp = [...permission]
													if ([4, 5, 6, 7].includes(p)) {
														tmp[i] -= 4
													} else {
														tmp[i] += tmp[i] > 0 ? 4 : 5
													}
													setPermission(tmp)
												}}
											/>
										</Stack>
									</Box>
								)
							})}
						</Box>
					</tbody>
				</table>
				<Box>
					<LoadingButton
						disabled={permission.join('') === group.permission.join('')}
						variant="outlined"
						loading={loading}
						startIcon={<SaveIcon />}
						type="button"
						onClick={async () => {
							try {
								setLoading(true)
								const { message, error } = await GroupApi.update({
									id,
									permission,
								})
								dispatch(
									UIAction.addGlobalMessage({
										message,
										type: error ? 'error' : 'success',
									})
								)
							} finally {
								setLoading(false)
							}
						}}
					>
						変更する
					</LoadingButton>
				</Box>
			</Stack>
			<Divider sx={{ my: 2 }} />
			<AccountList groupId={group.id} accounts={group.accounts} />
		</Stack>
	)
}
