import { Stack, Typography } from '@mui/material'
import { MdOutlineAssignmentTurnedIn, MdRemoveCircleOutline } from 'react-icons/md'

interface Props {
	text: string
	active: boolean
	handleClick: () => void
}

export default function PermissionItem({ active, text, handleClick }: Props) {
	return (
		<Stack
			direction="row"
			justifyContent="center"
			sx={{
				bgcolor: active ? '#169500' : '#eee',
				color: active ? '#fff' : '#ccc',
				p: 1,
				my: 0.5,
				position: 'relative',
				cursor: 'pointer',
				overflow: 'hidden',
				'&:hover': {
					'& .action': {
						opacity: 1,
						pointerEvents: 'all',
						left: 0,
					},
				},
			}}
			onClick={handleClick}
		>
			<Typography
				sx={{
					userSelect: 'none',
				}}
			>
				{text}
			</Typography>
			<Typography
				sx={{
					color: active ? '#f40' : '#169500',
					bgcolor: '#fffa',
					transition: 'all 200ms',
					position: 'absolute',
					top: 0,
					left: '100%',
					width: '100%',
					height: '100%',
					opacity: 0,
					pointerEvents: 'none',
					fontSize: 24,
					display: 'flex',
					justifyContent: 'center',
					alignItems: 'center',
				}}
				className="action"
			>
				{active ? <MdRemoveCircleOutline /> : <MdOutlineAssignmentTurnedIn />}
			</Typography>
		</Stack>
	)
}
