import { Button, Chip, Stack, TextField, Typography } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import { ACCOUNT_API } from 'API/account'
import DeleteButton from 'components/Button/Delete'
import DialogConfirm from 'components/DialogConfirm'
import { IAccount } from 'interfaces'
import { Fragment, useEffect, useState } from 'react'
import { AiOutlineUserAdd } from 'react-icons/ai'
import { CiEdit } from 'react-icons/ci'
import { Link, useLocation } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'

interface Props {
	groupId: number
	accounts: IAccount[]
}
export default function AccountList({ groupId, accounts }: Props) {
	const dispatch = useAppDispatch()
	const location = useLocation()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const [loading, setLoading] = useState(false)
	const [selected, setSelected] = useState<{
		account: IAccount
		action: 'update' | 'active' | 'disabled' | 'deleted'
	}>()
	const [name, setName] = useState('')
	useEffect(() => {
		if (selected) {
			if (selected.action === 'update') setName(selected.account.name)
			else setName('')
		}
	}, [selected])
	if (!tokenInfo) return null
	return (
		<Stack gap={2} bgcolor="#eee" border="1px solid #ccc" p={1}>
			{selected && (
				<DialogConfirm
					open={!!selected}
					loading={loading}
					handleClose={() => {
						setSelected(undefined)
					}}
					handleSubmit={async () => {
						try {
							setLoading(true)
							const { id } = selected.account
							const { message, error } = await ACCOUNT_API.update({
								id,
								name,
								action: selected.action,
							})
							dispatch(
								UIAction.addGlobalMessage({
									message,
									type: error ? 'error' : 'success',
								})
							)
						} finally {
							setLoading(false)
						}
						setSelected(undefined)
					}}
					title={selected.action === 'update' ? '編集' : '確認'}
					maxWidth="xs"
					primaryButtonText={
						selected.action === 'update'
							? '保存する'
							: selected.action === 'active'
							? '有効にする'
							: selected.action === 'deleted'
							? '削除する'
							: '無効にする'
					}
					primaryButtonColor="warning"
				>
					{selected.action === 'update' ? (
						<TextField
							autoFocus
							variant={'standard'}
							required
							margin="none"
							size="small"
							fullWidth
							label={'名前'}
							value={name}
							onChange={(e) => setName(e.target.value)}
						/>
					) : (
						`${selected.account.email}を${
							selected.action === 'active' ? '有効' : selected.action === 'deleted' ? '削除' : '無効に'
						}してもよろしいですか`
					)}
				</DialogConfirm>
			)}
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					color="primary.dark"
					variant="h6"
					sx={{
						fontWeight: 600,
					}}
				>
					メンバー情報
				</Typography>
				<Link to={`/group/${groupId}/account/create`} state={{ location }}>
					<Button
						variant="outlined"
						sx={{
							minWidth: 120,
						}}
					>
						<AiOutlineUserAdd />
						新規作成
					</Button>
				</Link>
			</Stack>
			<DataGrid
				rows={accounts}
				columns={[
					{ field: 'email', headerName: 'メールアドレス', flex: 3 },
					{
						field: 'name',
						headerName: '名前',
						flex: 2,
						renderCell: (v) => {
							if (v.row.status === 'deleted') return v.row.name
							return (
								<>
									<Typography>{v.row.name}</Typography>
									<Button
										sx={{
											flex: 'none',
											mx: 1,
											p: 0.2,
											minWidth: 0,
										}}
										size="small"
										variant="outlined"
										color="info"
										onClick={() => {
											setSelected({
												account: v.row,
												action: 'update',
											})
										}}
									>
										<CiEdit className="text-lg" />
									</Button>
								</>
							)
						},
					},
					{
						field: 'status',
						headerName: 'ステータス',
						flex: 1,
						renderCell: (v) => {
							const status = v.row.status
							if (status === 'disabled') return <Chip size="small" color="error" label="ロック中" variant="filled" />
							else if (status === 'active') return <Chip size="small" label="有効" color="success" variant="filled" />
							else return null
						},
					},
					{
						field: '',
						headerName: 'アクション',
						flex: 2,
						renderCell: (v) => {
							const status = v.row.status
							if (tokenInfo.id === v.row.id) return '自分のアカウント'
							return (
								<Fragment key={v.id}>
									<Button
										sx={{
											mx: 0.5,
											px: 1,
											py: 0.2,
											minWidth: 0,
											fontSize: 13,
										}}
										color={status === 'disabled' ? 'success' : 'warning'}
										variant="outlined"
										onClick={() => {
											setSelected({
												account: v.row,
												action: status === 'disabled' ? 'active' : 'disabled',
											})
										}}
									>
										{status === 'disabled' ? '有効化' : '無効化'}
									</Button>
									<Link to={`/group/${groupId}/change/account/${v.row.id}`} state={{ location }}>
										<Button
											sx={{
												mx: 0.5,
												px: 1,
												py: 0.2,
												minWidth: 0,
												fontSize: 13,
											}}
											size="small"
											variant="outlined"
											color="info"
										>
											グループ変更
										</Button>
									</Link>
									<DeleteButton
										onClick={() => {
											setSelected({
												account: v.row,
												action: 'deleted',
											})
										}}
									>
										削除
									</DeleteButton>
								</Fragment>
							)
						},
					},
				]}
				initialState={{
					pagination: {
						paginationModel: { page: 0, pageSize: 10 },
					},
				}}
				pageSizeOptions={[10, 20, 30, 50]}
				sx={{
					'& .MuiDataGrid-cell:focus': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:focus-within': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:hover': {},
					'& .MuiDataGrid-columnHeaders': {
						bgcolor: 'primary.dark',
						color: '#fff',
					},
					'& .MuiDataGrid-columnHeaders svg': {
						color: '#fff',
					},
				}}
				density="compact"
				hideFooterSelectedRowCount
				editMode="row"
				disableColumnMenu
				disableRowSelectionOnClick
			/>
		</Stack>
	)
}
