import axiosClient from 'utils/axiosClient'

export const ACCOUNT_API = {
	async getOne(id: number | string) {
		const {
			data: { account },
		} = await axiosClient.get(`/api/account/${id}`)
		return account
	},
	async create(body: { type: 'Default' | 'MSAL'; groupId: number; name: string; email: string; password?: string }) {
		const { data } = await axiosClient.post<{ id: number; error: boolean; message: string }>('/api/account/', body)
		return data
	},
	async update({
		id,
		name,
		action,
	}: {
		id: number | string
		name?: string
		action?: 'update' | 'active' | 'disabled' | 'deleted'
	}) {
		const {
			data: { error, message },
		} = await axiosClient.put<{ error: boolean; message: string }>('/api/account/' + id, {
			name,
			action: name ? undefined : action,
		})
		return { error, message }
	},
	async updateGroup(id, groupId) {
		const { data } = await axiosClient.patch<{ error: boolean; message: string }>(`/api/account/${id}/change-group`, {
			groupId,
		})
		return data
	},
}
